.slide__images {
	width: 100%;
    display: inline-block;
    margin: 0 auto;
}
.slider_promo .bx-pager {
    position: absolute;
    bottom: 20px;
    right: 9%;
}
@media screen and (max-width: 768px){
	.slider_promo .bx-pager {
		display:none;
	}
}
.slider_promo .bx-pager-item a{
	background: #adadad;
	text-indent: -9999px;
    display: block;
    width: 10px;
    height: 10px;
    margin: 0 3px;
    outline: 0;
    border-radius: 50%;
    border: 3px solid #adadad;
}
.slider_promo  .bx-pager-item a:hover,
.slider_promo  .bx-pager-item a.active {
    background: #2190c1;
    border: 3px solid #f98561;
}
.slider_testimonials  .bx-pager-item a {
    background: #c1c1c1;
    text-indent: -9999px;
    display: block;
    width: 120px;
    height: 12px;
	border-radius: 0;
}
.slider_testimonials .bx-pager-item a:hover,
.slider_testimonials .bx-pager-item a.active{
	background: #f98561;
}
.slider_testimonials .bx-pager-item {
	margin:0 9px;
}
.slider_testimonials .bx-pager-item:first-child,
.slider_testimonials .bx-pager-item:last-child{
	margin:0;
}
.slider_testimonials .bx-pager-item a.active {
	position:relative;
}
.slider_testimonials .bx-pager-item a.active:before {
    content: "";
    position: absolute;
    left: 50%;
    top: -50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    border-bottom: 6px solid #f98561;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
}
.slider_testimonials .bx-pager {
	position: absolute;
	bottom: -30px;
	width: 100%;
	right: 0;
}
.slide_testimonials  img {
    max-width: 100%;
    display: inline-block;
    margin: 0 auto;
    border-radius: 50%;
	transition:0.3s ease;
	border:2px solid transparent;
}
.slide_testimonials img:hover {
	border:2px solid #f98561;
	box-shadow: 0 0 3px 3px #ffdbff;
	transition:0.3s ease;
}
/** RESET AND LAYOUT
===================================*/
@media screen and (max-width: 480px){
	.slider_testimonials .bx-pager-item{
		width:31%;
	}
	.slider_testimonials .bx-pager-item a{
		width:100%;
	}
}
.bx-wrapper {
	position: relative;
	margin: 0 auto -5px;
	padding: 0;
}
@media screen and (max-width: 320px){
	.slider_testimonials .bx-pager {
		display:none;
	}
}


/** THEME
===================================*/

.bx-wrapper .bx-viewport {
	/*-moz-box-shadow: 0 0 5px #ccc;
	-webkit-box-shadow: 0 0 5px #ccc;
	box-shadow: 0 0 5px #ccc;
	border:  5px solid #fff;
	background: #fff;
	left: -5px;*/


	/*fix other elements on the page moving (on Chrome)*/
	-webkit-transform: translatez(0);
    	transform: translatez(0);
}

.bx-pager,
.bx-wrapper .bx-controls-auto {
    position: absolute;
    bottom: 20px;
    right: 50px;
}
/* LOADER */

.bx-wrapper .bx-loading {
	min-height: 50px;
	background: url(../images/bx_loader.gif) center center no-repeat #fff;
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2000;
}

/* PAGER */

.bx-wrapper .bx-pager {
	text-align: center;
	font-size: .85em;
	font-family: Arial;
	font-weight: bold;
	color: #666;
	padding-top: 20px;
}

.bx-wrapper  .bx-pager-item,
.bx-wrapper  .bx-controls-auto-item {
	display: inline-block;
}
/*no CSS*/
/* DIRECTION CONTROLS (NEXT / PREV) */

.bx-wrapper .bx-prev {
	left: 10px;
	background: url(../images/controls.png) no-repeat 0 -32px;
}

.bx-wrapper .bx-next {
	right: 10px;
	background: url(../images/controls.png) no-repeat -43px -32px;
}

.bx-wrapper .bx-prev:hover {
	background-position: 0 0;
}

.bx-wrapper .bx-next:hover {
	background-position: -43px 0;
}

.bx-wrapper .bx-controls-direction a {
	position: absolute;
	top: 50%;
	margin-top: -16px;
	outline: 0;
	width: 32px;
	height: 32px;
	text-indent: -9999px;
	z-index: 9999;
}

.bx-wrapper .bx-controls-direction a.disabled {
	display: none;
}

/* AUTO CONTROLS (START / STOP) */

.bx-wrapper .bx-controls-auto {
	text-align: center;
}

.bx-wrapper .bx-controls-auto .bx-start {
	display: block;
	text-indent: -9999px;
	width: 10px;
	height: 11px;
	outline: 0;
	background: url(../images/controls.png) -86px -11px no-repeat;
	margin: 0 3px;
}

.bx-wrapper .bx-controls-auto .bx-start:hover,
.bx-wrapper .bx-controls-auto .bx-start.active {
	background-position: -86px 0;
}

.bx-wrapper .bx-controls-auto .bx-stop {
	display: block;
	text-indent: -9999px;
	width: 9px;
	height: 11px;
	outline: 0;
	background: url(../images/controls.png) -86px -44px no-repeat;
	margin: 0 3px;
}

.bx-wrapper .bx-controls-auto .bx-stop:hover,
.bx-wrapper .bx-controls-auto .bx-stop.active {
	background-position: -86px -33px;
}

/* PAGER WITH AUTO-CONTROLS HYBRID LAYOUT */

.bx-wrapper .bx-controls.bx-has-controls-auto.bx-has-pager .bx-pager {
	text-align: left;
	width: 80%;
}

.bx-wrapper .bx-controls.bx-has-controls-auto.bx-has-pager .bx-controls-auto {
	right: 0;
	width: 35px;
}

/* IMAGE CAPTIONS */

.bx-wrapper .bx-caption {
	position: absolute;
	bottom: 0;
	left: 0;
	background: rgba(80, 80, 80, 0.75);
	width: 100%;
}

.bx-wrapper .bx-caption span {
	color: #fff;
	font-family: Arial;
	display: block;
	font-size: .85em;
	padding: 10px;
}









































































































